  }
  
  .loader {
    width: 48px;
    height: 48px;
    border: 5px solid #FFF;
    border-bottom-color: #FF3D00;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    animation: rotation 0.7s linear infinite;
    position: absolute;
    top: 50%;
    left: 50%;
    /* прячет тег от пользователя но оставляет в dom дереве */
    /* если тег занимает место в верстке - он продолжит его занимать  */
    /* visibility: hidden; */
    /* прячет тег из dom дерева */
    display: none;
  }
  
  @keyframes rotation {
    0% {
      transform: rotate(0deg);
    }
  
    100% {
      transform: rotate(360deg);
    }
  }